import { useState } from "react";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import { useFinanceAllotScholarshipExcel } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
// import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../Ui/Button/Button.module.css";
import { useTranslation } from "react-i18next";
import ExcelImport from "../../../../ExcelImport/ExcelImport";
import { academicRequiredFormat } from "../../../../Constant/Excel/scholarship_excel";
const ScholarshipMapping = ({ aid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [excel, setExcel] = useState(false);
  const [financeAllotScholarshipExcel] = useFinanceAllotScholarshipExcel();
  const [fileUpload] = useFileUpload();

  const onExcelFileUpload = (e) => {
    if (e.target?.files?.length && aid) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            financeAllotScholarshipExcel({
              aid: aid,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onExcelUpload = () => {
    setExcel((pre) => !pre);
  };
  const onImportFileInSystem = (list, count) => {
    // console.info("dsfdgd", list, count);
    onExcelUpload();
    setDisabled(true);
    //   oneSubjectExcelUpload({
    //     sid: getQuery.state?.subjectId,
    //     excelFile: {
    //       excel_file: list,
    //       excel_count: count,
    //     },
    //   })
    //     .then(() => {
    //       setDisabled(false);
    //     })
    //     .catch({});
  };
  return (
    <>
      {/* <UploadExcelButton
        dynamicKey="scholashipAllottment"
        onExcelFileUpload={onExcelFileUpload}
        title="Scholarship Allottment Excel"
        label="Allottment Excel"
      /> */}

      <label className={style.upload_excel_file} onClick={onExcelUpload}>
        <img
          src={`${assestsUserFinanceUrl}/upload-xslx.svg`}
          alt="add student icon"
          title={"Scholarship Allottment Excel"}
        />
        <h6>{t("Allottment Excel")}</h6>
      </label>
      {disabled && <QvipleLoader />}
      {excel && (
        <ExcelImport
          onClose={onExcelUpload}
          onImportFileInSystem={onImportFileInSystem}
          requiredFormat={academicRequiredFormat}
        />
      )}
    </>
  );
};

export default ScholarshipMapping;
